@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;400&display=swap');

html {
  scroll-behavior: smooth;
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Inter',sans-serif;
}
a:hover {
  cursor: pointer;
}
label {
  display: block;
  margin-bottom: 0.5rem;
  color:rgb(113, 128, 150)
}
.form  input 
{
  padding: 0.5rem;
  border-radius: 6px;
  margin-bottom: 1rem;
}
.links p:hover 
{
  cursor: pointer;
}
.links p 
{
  font-weight: 700;
  
}

.fg--search {
  
  position: relative;
  
}

span {
  /* color:#667eea */
}
span.last{
  color:rgb(113,128,150)
}
span:hover {
  /* background-color: #667eea; */
  color:#fff;
  border-radius: 9999px;
}
.link:hover {
  color:rgb(102, 126, 234)
}

.fg--search button {
  background: transparent;
  border: none;
  cursor: pointer;
  display: inline-block;
  font-size: 20px;
  position: absolute;
  top: 0;
  left: 0;
  padding: 10px 20px;
  z-index: 2;
}
.toggle-button button 
{
  width:50px;
  height: 24px;
  border-radius: 20px;
}
.circle
{
  width:21px;
  height: 21px;
  background-color: white;
  border: 2px solid black;
}
.toggle-button .light
{
  background: linear-gradient(45deg,#FF7D6B,#FFD64C);
}
.toggle-button .dark
{
  background: linear-gradient(45deg,#3366FF,#AC15B8);
}
.box:hover .icon {
  color:rgb(113, 128, 150);
}
.box 
{
  transition: transform 250ms ease, box-shadow 250ms ease, color 250ms ease;
  box-shadow: 1px 1px 5px 0 rgb(1 1 1 / 5%)
}
.box:hover
{
  cursor: pointer;
  transform: translateY(-5px) ;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 20px 25px -5px, rgba(0, 0, 0, 0.04) 0px 10px 10px -5px;
}

.card-content {
  transition: transform 250ms ease, box-shadow 250ms ease, color 250ms ease;
  box-shadow: 0px 2px 4px rgb(46 41 51 / 8%), 0px 5px 10px rgb(71 63 79 / 16%)
  }
.card-content:hover {
  cursor: pointer;
  transform: translateY(-5px) ;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 20px 25px -5px, rgba(0, 0, 0, 0.04) 0px 10px 10px -5px;

}

.single-card-content {
  transition: transform 250ms ease, box-shadow 250ms ease, color 250ms ease;
  box-shadow: 1px 1px 5px 0 rgb(1 1 1 / 5%)
}
.single-card-content:hover {
  cursor: pointer;
  transform: translateY(-5px) ;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 20px 25px -5px, rgba(0, 0, 0, 0.04) 0px 10px 10px -5px;

}

.backgroundImage {
  background-image: url('/public/images/banner.png');
  padding: 2rem;
}


.content h2 {
  font-size: 20px;
}
.download {
  padding:8px 16px
}

.margin-top-container {
  margin : 4rem 0;
  border-top-width: 2px;
  border-color: transparent;
}
.margin-bottom-container
{
  margin:2rem 0;
  border-top-width: 2px;
  border-color: transparent;
}
.straight-line
{
  width: 5px;
  height: 32px;
  background-color: rgb(226, 232, 240);
  margin-right: 27px;
}

/* first svg */
.css-l9ytao {
  font-size: 9rem;
  color: #9f7aea;
}

.css-1iew42x {
  font-size: 7rem;
  -webkit-transform: rotate(120deg);
  -ms-transform: rotate(120deg);
  transform: rotate(120deg);
  margin-top: 0.5rem;
  margin-left: -3rem;
}
.subscribe-heading{
  font-size: 24px;
  line-height:31.92px ;
}
.banner-padding{
  padding:0 32px
}

.card {
  padding:8px;
  width: 100%;
}

.css-5fuvjv  {
  color:#9f7aea;
  width: 64px;
  height: 64px;
  display: block;
  margin-bottom: 1rem;
}

.description :not(:last-child) + ::before {
  content: " ・ ";
}
.time > *:not(:last-child) + *:before {
  content: " ・ ";
}

.articles{
  transition: transform 250ms ease, box-shadow 250ms ease, color 250ms ease;
  box-shadow: 1px 1px 5px 0 rgb(1 1 1 / 5%)
}
.articles:hover {
  cursor: pointer;
  transform: translateY(-5px) ;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 20px 25px -5px, rgba(0, 0, 0, 0.04) 0px 10px 10px -5px;

}


.contact-button:hover{
  background-color: rgb(102, 126, 234);
}
.contact-button:hover .icon{
  color: white;
}

.blogs-heading h1 {
  line-height: 66.8px;
}

.heading-margin-top{
  margin: 32px 0;
}

.view-more button
{
  color: #718096;
  background-color: #e2e8f0;
}
.view-more button:hover{
  color:rgb(226, 232, 240);
  background-color:rgb(113, 128, 150) ;
}

.tags-link
{
  display: flex;
  align-items:center;
  justify-content:center;
}


.light-mode-button:hover 
{
  background-color: rgb(102, 126, 234);
}
.dark-mode 
{
  background-color: transparent;
  color:#c3c9cf
}
.light-mode 
{
  background-color: #EDF2F7;
}
.light-mode-svg {
  color:#c3c9cf;
}
.dark-mode-svg{
  color:#c3c9cf;
}

.light-mode-button:hover .dark-mode-svg{
  color:white;
} 
.light-mode-button:hover .light-mode-svg  {
  color:white;
}


.dark-link 
{
  color:#e2e8f0;
}
.light-link 
{
  color:#718096
}

.main-menu li:hover 
{
  cursor: pointer;
  color: rgb(102, 126, 234);
}


.gatsby svg 
{
  margin-left: 0.5rem;
}

.dark-icon {
  color:rgb(113, 128, 150)
}
.light-icon 
{
  color: ;
}



@media (max-width:768px){
  .tags-link 
  {
   display: block;
  }
  .backgroundImage{
    background-image: none;
    padding: 17px;
  }
  .content h2 {
    font-size: 17px;
  }
  .month{
    padding-bottom: 13.6px;
  }
  .download{
    padding: 6.8px 13.6px;
    font-size: 13.6px;
  }
  .margin-top-container{
    margin: 27px 0
  }
  .margin-bottom-container{
    margin: 13.6px;
  }
  .category-heading{
    width:100%
  }
  .subscribe-section
  {
    padding:0 13.6px
  }
  .text-center
  {
    font-size: 13.6px;
    line-height: 24.48px;
    width: 401px;
  }
  .subscribe-heading{
    font-size: 20.4px;
    line-height: 27.132px;
    font-weight: 700;
  }
  .banner-padding{
    padding:0 13.6px;
  }
  .blogs-heading h1 {
    line-height:33.915px ;
  }
  .heading-margin-top{
    margin : 16px 0;
  }
  .description{
    font-size: 13.3px;
  }
   .red {
   color  : rgb(245, 101, 101);
  }
  
}